import React, {Component} from "react"
import Java from "../icons/java.svg"
import Python from "../icons/python.svg"
import Slack from "../icons/slack.svg"
import M from  'materialize-css/dist/js/materialize.min.js';


class Progress extends Component {
    state = {
        refreshed: false,
        states:[
        {
        image:Java,
        key: 1,
        title:"Java",
        percent:"20%",
        content:"Javascript has a nicer, stricter syntax."},
        {
        image:Python,
        key: 2,
        title:"Python",
        percent:"50%",
        content:"I can create functions and classes in Python."},
        {
        image:Slack,
        key: 3,
        title:"Slack",
        percent:"100%",
        content:"I am the best slacker in the entire world."}
    ]}

    componentDidMount = () => {
        Progress.loadJs();
    }

    static loadJs(){
        var elems = document.querySelectorAll('.collapsible');
        M.Collapsible.init(elems, {});
    }

    render(){
        const progressL = this.state.states;
        const progressList = progressL.length?
            progressL.map(
                progress => {
                    return(
                        <ProgressBar percent={progress.percent} title = {progress.title} content = {progress.content} key={progress.key} image = {progress.image}/>
                    )
                }):(
                    <div className="center">
                        No skills yet!
                    </div>);
        return(
        <div>
            <div id="modded" className="row">
                <div className="col s12">
                    <ul className="collapsible">
                        {progressList}
                    </ul>
                </div>	
            </div>
        </div>
        )
    }
}

class ProgressBar extends Component{
    render(){
        const {percent, title, content, image} = this.props;
        const style = {
            width: percent
        }
        return(
            <React.Fragment>
                <li>
                    <img src={image}/>
                    <div className="collapsible-header">
                        
                        <div className="progress blue lighten-4 round valign-wrapper">
                            <span>{title}</span>
                            
                            <div className="determinate blue" style={style}><b>{percent}</b></div>
                        </div>

                    </div>
                    <div className="collapsible-body white-text">{content}</div>
                </li>
            </React.Fragment>
        )
    }
}
export default Progress