import React, {Component} from 'react'

class Quote extends Component{
    state = {
        current: "A computer once beat me at chess, but it was no match for me at kick boxing.",
        quote: [
            "I do not fear computers. I fear lack of them.", "A computer once beat me at chess, but it was no match for me at kick boxing.",
            "The computer was born to solve problems that did not exist before.", "Computer Science is no more about computers than astronomy is about telescopes.",
            "Software is a gas; it expands to fill its container.", "Standards are always out of date.  That’s what makes them standards.",
            "It’s hardware that makes a machine fast.  It’s software that makes a fast machine slow."
            ]
        }
    randomlyGenerateQuote = () => {
        let number = this.state.quote.length;
        let randomNumber = Math.floor(Math.random() * number);
        this.setState({
            current: this.state.quote[randomNumber]
        });
    }
    render(){
        return(
            <React.Fragment>
                <div className="row">
                    <div className="col l12 m12 s12">
                        <h4 className="center white-text home-header">Quote of the day</h4>
                    </div>
                    
                    <div className="container">
                        <div className="blockquote-wrapper">
                            <div className="blockquote" onClick={this.randomlyGenerateQuote}>
                                <h1 className="flow-text">
                                {this.state.current}
                                </h1>
                                <h4>&mdash; Tan Jun En<br/><em>Proud Plagerizer of Free Frontend</em></h4>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Quote;