import React, {Component} from "react"
import NDSC from "../icons/ndsc.png"

// {title, content, image}
class Accomplishment extends Component {

    componentDidMount = () => {
    }    

    render(){
        return (
            <div className="card hover-reveal">
            <div className="card-image waves-effect waves-block waves-light">
                <img className="activator icon" src={NDSC}/>
            </div>
            <div className="card-content">
                <span className="card-title activator grey-text text-darken-4">Card Title</span>
            </div>
            <div className="card-reveal">
                <span className="card-title grey-text text-darken-4">Card Title<i className="material-icons right">close</i></span>
                <p>Here is some more information about this product that is only revealed once hover it</p>
            </div>
            </div>
        )
    }
}

export default Accomplishment