import React, {Component} from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import Navbar from './components/Navbar'
import Footer from './components/Footer'
import Contact from "./components/Contact"
import About from "./components/About"
import Home from "./components/Home"
import "./index.css"

class App extends Component{
  
  state = {
    top: 0,
    left: 0,
    height: 0,
    width: 0
  }
  constructor(props) {
    super(props);
    this.mains = React.createRef();
  }

  getMainReact= () => {
    this.setState({
      height:this.mains.current.offsetHeight,
      width:this.mains.current.offsetWidth,
      left: this.mains.current.offsetLeft,
      top:this.mains.current.offsetTop
    });
  }

  componentDidMount = () => {
    this.getMainReact();
  }

  render(){

    return (
      <BrowserRouter>
        <div className="App">
          <header>
            <Navbar/>
          </header>
            <main ref = {this.mains}>
              <div>
              <Switch>        
                <Route exact path="/about" component = {About}/>
                <Route exact path="/contact" component = {() => <Contact parentHeight={this.state.height}/>}/>
                <Route exact path="/" component = {() => <Home parentHeight={this.state.height}/>}/>
                <Route exact path="/resume" component = {() => <Home parentHeight={this.state.height}/>}/>
                {/* <Route path="/:post_id" component = {Post}/> */}
              </Switch>
              </div>
          </main>
          
          <Footer/>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
