import React, {Component} from 'react'
import {Link, NavLink, withRouter} from 'react-router-dom'
import M from  'materialize-css/dist/js/materialize.min.js';
import Card from "./Card";

class Navbar extends Component{

    state = {
        home: null,
        contact: null,
        about: null
    }

    componentDidMount = () => {
        let sidenav = document.querySelector('#mobile-collapse');
        M.Sidenav.init(sidenav, {});
        
    }

    static getDerivedStateFromProps = (props, state) => {
        if (props.location.pathname.slice(1)){ 
            return ({
                home: null,
                contact: null,
                about: null,
                [props.location.pathname.slice(1)]: "active"
            })
        }
        else{
            return ({
                home: "active",
                contact: null,
                about: null,
            })
        }
    }


      
    render(){
        return ( 
            <nav>
                <div className="nav-wrapper navbar navspacing blue-grey darken-4">
                    <Link to="/" className="brand-logo"><i className="small-1 material-icons">work</i>Resume</Link>
                    <a href="#" data-target="mobile-collapse" className="sidenav-trigger"><i className="medium material-icons">menu</i></a>
                    
                    {/* Non-compressed menu */}
                    <ul className = "right hide-on-med-and-down">
                        <li className={this.state.home}> <Link to="/" >Home</Link></li>
                        <li className={this.state.about}> <Link to="/about">About</Link></li>
                        <li className={this.state.contact}> <Link to="/contact">Contact</Link></li>
                    </ul>

                    {/* Compressed menu */}
                    <ul className="sidenav" id = "mobile-collapse">
                        <li><Card/></li>
                        <li><Link className="subheader" to ="#">My information</Link></li>
                        <li><div className="divider"></div></li>
                        <li className={this.state.home}><Link to="/" className="sidenav-close">Home</Link></li>
                        <li className={this.state.about}><Link to="/about" className="sidenav-close">About</Link></li>
                        <li className={this.state.contact}><Link to="/contact" className="sidenav-close">Contact</Link></li>
                    </ul>

                </div>
            </nav>
        );
    }
}

export default withRouter(Navbar)