import React, {Component} from 'react'

const Experience = ({title, content, date, skills}) => {
    let mySkills = skills.map(skill => {
        return(<li className="inlining" key= {Math.random()}><div className="chip chip-font-size">{skill}</div></li>)
    })
    return(
        <div className ="experience">
            <div className = "row ">
            <div className ="tag"></div>
            </div>
            <div className = "row" >
                <div className = "col l8 m8 s12 left-align" style= {{paddingBottom:"1%"}}>
                    <h5 className="blue-text text-darken-4">{title}</h5>
                    <div className = "date"><i className="blue-text">{date}</i></div>
                    <div className ="container">
                    </div>
                </div>
                <div className= "col l4 m4 s11 offset-s1" style= {{paddingBottom:"1%"}}>        
                    <ul>
                        {mySkills}
                    </ul>
                </div>
               
            </div>
            <p className ="flow-text experience-p" style= {{margin:"20px"}}>{content}</p>
        </div>
    )
}

class Experiences extends Component{
    state = {
        experiences : [
            {
                key: 1,
                title: "Android Developer Intern",
                date: "AEvice Health | 2018-2019",
                content: "Creation of the UI and UX of Android application for the company. Integrated application with Amazon Web Services.",
                skills: ["Java", "D3", "Android Studios", "PHP"]
            },
            {
                key: 2,
                title: "Media Analyst Intern",
                date: "Meltwater | 2016",
                content: "Assisted Media Analyst in data collecting and data consolidation. Learned and contributed best practices in quantitative and qualitative techniques. Trained in media analysis and crisis management. Learned inner workings of mainstream and social media landscape. Analyzed media content to achieve meaningful insights",
                skills: ["Data Analytics", "Media Analysis"]
            },
            {
                key: 3,
                title: "Software Developer Intern",
                date: "AEvice Health | May 2019 - June 2019",
                content: "Helped with the writing and contribution of a research paper for an acoustic sensor system for the company. Programmed embedded systems modules, which included RENESAS MQTT and WIFI modules.",
                skills: ["Python", "AWS", "IoT", "MQTT", "Research"]
            }
        ]
    }

    render(){
        let editedExperiences = this.state.experiences.map(experience =>{
            return (
                <Experience title={experience.title} content={experience.content} key = {experience.key} skills={experience.skills} date={experience.date}/>
            )
        })
        return(
            <div className="container">
                {editedExperiences}
            </div>
        )
    }
}

export default Experiences;