import React, {Component} from 'react'
import User from "../pika.png"
import {Link} from "react-router-dom"

class Card extends Component {

    render(){
        return(
            <div>
                <div className="card">
                        <div className="card-image">
                            <img src={User} />
                        </div>
                        <div className="card-content">
                            <p className= "black-text">Computer Science Graduate in NTU. This button essentially goes to the contact page. <Link className=" btn-small waves-effect sidenav-close" to = "/contact">No Social Distancing!</Link></p>
                        </div>
                </div>
            </div>    
        );
    }
}

export default Card;