import React, {Component} from 'react'
import Progress from "./Progress"
import Quote from './Quote';
import Resume from "../pdf/Resume.pdf"
import Patreon from "../icons/patreon.png"
import Accomplishment from './Accomplishment';

class Home extends Component {
    render(){
        const {parentHeight} = this.props;
        const style = {
            height: parentHeight,
            padding: "0.5%"
        }
        console.log(style);
        return (
            <React.Fragment>
                <div className="row" style={style}>
                    <div className="col s12 m4 l4 blue-grey darken-3 hovering" >
                        <div className="row">
                            <div className="col l12 m12 s12 valign-wrapper">
                                <Quote/>
                            </div>
                            <div className="col l12 s12 m12" >
                                <h4 className="center white-text home-header">Skills</h4>
                                <Progress/>
                            </div>
                        </div>
                        <div className="row ">
                            <div className="col s12 m12 l12 center" >
                                <h4 className="white-text home-header ">Accomplishments</h4>
                                <div className="row">
                                    <div className="col l12 s12 m12">
                                        <div className="card deep-orange darken-1">
                                            <div className="card-content">
                                                <div className="card-title"><span style = {{fontWeight: "400"}} className= "activator white-text home2-span left-align valign-wrapper ">Champion Team for Yitu Hack-AI-thon Challenge 2019<i className="material-icons right">more_vert</i></span></div>
                                            </div>
                                            <div className="card-reveal deep-orange lighten-3">
                                            <div className="card-title"><span style = {{fontWeight: "400"}} className= "activator home2-span left-align valign-wrapper"><blockquote>Champion Team for Yitu Hack-AI-thon Challenge 2019</blockquote><i className="material-icons right">close</i></span></div>
                                                <p className = " home2-span-small left-align">Participated and won the 2019 Yitu Hack-Ai-thon hosted by YITUTech. Our team utilized several computer vision and object detection techniques in the competition, which included feature-pyramid networks, SSD and several data pre-processing techniques.</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col l12 s12 m12">
                                        <div className="card teal darken-1">
                                            <div className="card-content">
                                                <div className="card-title"><span style = {{fontWeight: "400"}} className= "activator white-text home2-span left-align valign-wrapper ">Most Creative Team for 2019 National Data Science Challenge<i className="material-icons right">more_vert</i></span></div>
                                            </div>
                                            <div className="card-reveal teal accent-1">
                                            <div className="card-title"><span style = {{fontWeight: "400"}} className= "activator home2-span left-align valign-wrapper"><blockquote>Most Creative Team for 2019 National Data Science Challenge</blockquote><i className="material-icons right">close</i></span></div>
                                                <p className = " home2-span-small left-align">Awarded the Most Creative Team Award for the 2019 National Data Science Challenge (Beginner's Section). Utilized a data pipeline of Machine Learning (SVM) and Deep Learning frameworks using Keras and Pytorch. Placed 8th out of 360 teams in the Kaggle Leaderboard.</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col l12 s12 m12">
                                        <div className="card blue darken-3">
                                            <div className="card-content">
                                                <div className="card-title"><span style = {{fontWeight: "400"}} className= "activator white-text home2-span left-align valign-wrapper ">Top Five Placement in Best Coder Challenge<i className="material-icons right">more_vert</i></span></div>
                                            </div>
                                            <div className="card-reveal blue accent-1">
                                            <div className="card-title"><span style = {{fontWeight: "400"}} className= "activator home2-span left-align valign-wrapper"><blockquote>Top Five Placement in Best Coder Challenge</blockquote><i className="material-icons right">close</i></span></div>
                                                <p className = " home2-span-small left-align">Participated and placed top five in a coding challenge hosted by Shopee.</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                            <div className="col s12 m12 l12 right-align">
                            <p style={{padding:"2%", paddingTop:"5%"}} className="flow-text \"><span className="white-text home-span">Download my resume: </span><a href = {Resume} download className="btn-floating btn-large blue waves-effect"><i className="material-icons">cloud_download</i></a></p>
                            <p style={{padding:"2%"}} className="flow-text"><span className="white-text home-span">Patreon! <font color="#FA8072"><i>Coming Soon!</i></font> </span><a href = {Resume} download className=" btn-floating disabled btn-large white center"><img src= {Patreon} className = "icon-small"/></a></p>
                                
                            </div>
                        </div>
                    </div>
                </div>
                
            </React.Fragment>
        )
    }
}

export default Home