import React from 'react';
import Pika from '../pika.png'
const Contact = ({parentHeight}) => {
    let style = {
        height: "70vh",
        paddingTop: "3%"
    }
    let style2 = {
        height: "70vh"
    }
    return (

        <div >
            <div className="row container"> 
                <div className="col l7 m7 s7" style={style}>
                    <h1 className = "align-left white-text accent-1 contact-h1">Contact Me</h1>
                    <p className="flow-text white-text accent-1"><i className="material-icons ">home</i><span className="contact-span"><b>Address:</b> Pasir Ris Street 11 Block 181</span></p>
                    <p className="flow-text align-left white-text accent-1"> <i className="material-icons">email</i><span className="contact-span"><b>Email:</b> tanjunen@ymail.com</span></p>
                    <p className="flow-text align-left white-text accent-1"> <i className="material-icons">phone</i><span className="contact-span"><b>Phone Number:</b> 96579895</span></p>
                    {/* <div className="card blue-grey darken-3">
                        <div className="card-content white-text">
                            <p className="contact-content"><i className="material-icons">home</i><span className="contact-span"> <b>Address:</b> Pasir Ris Street 11 Block 181</span></p>
                            <p className="contact-content"> <i className="material-icons">email</i><span className="contact-span"><b>Email:</b> tanjunen@ymail.com</span></p>
                            <p className="contact-content"> <i className="material-icons">phone</i><span className="contact-span"><b>Phone Number:</b> 96579895</span></p>
                        </div>
                    </div> */}
                </div>
                <div className="col l5 m5 s5 valign-wrapper" style={style2}>
                    <div className="container  cover">
                        <img src={Pika} />
                    </div>
                </div>
            </div>
            
            
        </div>
    )
}

export default Contact;