import React from 'react'
import "../index.css"
import "./footer.css"
import Instagram from "../icons/instagram.png"
import Linkedin from "../icons/linkedin.png"
import Facebook from "../icons/facebook.png"

const Footer = (props) => {
    return(
        <div className = "footer">
            <footer className="page-footer blue-grey darken-4">
                <div>
                    <div className="row footerdiv">
                        <div className="col l2 m4 hide-on-small-only">
                            <h5 className="white-text shift flow-text">My Social Media</h5>
                        </div>
                        <div className="col l3 offset-l6 s8 m6">
                            <ul className="footerlist">
                            <li className="right-align logo">
                                <div>
                                    <section className="ss-icon">
                                        <a href="https://www.facebook.com/tan.junen" className="icon icon-1"><article><span className="icon">facebook</span></article></a>    
                                    </section>
                                </div>
                            </li>
                            <li className="right-align logo">
                                <section className="ss-icon">
                                    <a href="https://www.linkedin.com/in/tan-jun-en-423148134/" className="icon icon-2"><article><span className="icon">linkedin</span></article></a>
                                </section>
                            </li>

                            <li className="right-align logo">
                                <section className="ss-icon">
                                    <a href="https://www.instagram.com/tanjunen/" className="icon icon-3"><article><span className="icon">instagram</span></article></a>
                                </section>
                            </li>

                                {/* <li className="right-align logo"><a href="https://www.instagram.com/tanjunen/"><img className = "icon" src = {Instagram}/></a></li>
                                <li className="right-align logo"><a href="https://www.linkedin.com/in/tan-jun-en-423148134/"><img className = "icon" src = {Linkedin}/></a></li>
                                <li className="right-align logo"><a href="https://www.facebook.com/tan.junen"><img className = "icon" src = {Facebook}/></a></li> */}
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="footer-copyright">
                    <div>
                        <p className="hide-on-med-and-down navspacing " style = {{paddingLeft:"26px"}}>Tan Jun En, 2020 <i className="small-1 material-icons">school</i></p>
                    </div>
                </div>
            </footer>
        </div>
    )
}

export default Footer